import { createFeatureSelector, createSelector } from '@ngrx/store';
import { INamedListDesignerModel, INamedListDesignerState } from './named-list-designer.types';
import { RX_NAMED_LIST_DESIGNER } from '../../named-list-designer.constant';

export const namedListDesignerStateSelector = createFeatureSelector<INamedListDesignerState>(
  RX_NAMED_LIST_DESIGNER.featureSelector
);

export const namedListDesignerModelSelector = createSelector(
  namedListDesignerStateSelector,
  (namedListDesignerState: INamedListDesignerState) => namedListDesignerState.model
);

export const bundleIdSelector = createSelector(
  namedListDesignerModelSelector,
  (namedListDesignerModel: INamedListDesignerModel) => namedListDesignerModel.bundleId
);

export const definitionNameSelector = createSelector(
  namedListDesignerModelSelector,
  (namedListDesignerModel: INamedListDesignerModel) => namedListDesignerModel.definitionName
);

export const isDesignModeSelector = createSelector(
  namedListDesignerModelSelector,
  (namedListDesignerModel: INamedListDesignerModel) => namedListDesignerModel.isDesignMode
);

export const definitionModelFromDefinitionSelector = createSelector(
  namedListDesignerModelSelector,
  (namedListDesignerModel: INamedListDesignerModel) => namedListDesignerModel.definitionModelFromDefinition
);

export const definitionModelSelector = createSelector(
  namedListDesignerModelSelector,
  (namedListDesignerModel: INamedListDesignerModel) => namedListDesignerModel.definitionModel
);

export const isDirtySelector = createSelector(
  namedListDesignerModelSelector,
  (namedListDesignerModel: INamedListDesignerModel) => namedListDesignerModel.isDirty
);

export const savedDefinitionNameSelector = createSelector(
  namedListDesignerModelSelector,
  (namedListDesignerModel: INamedListDesignerModel) => namedListDesignerModel.savedDefinitionName
);

export const originalDefinitionSelector = createSelector(
  namedListDesignerModelSelector,
  (namedListDesignerModel: INamedListDesignerModel) => namedListDesignerModel.originalDefinition
);
